import { CollisionConfig, BallSetting } from './types'
import Ball from './ball'
import debounce from 'lodash.debounce'
import { setupCanvas, random, randomRgba, resizeEvent, isMobile } from './utils'

class Collision {
  private ctx?: CanvasRenderingContext2D
  private raf?: number
  private balls: Ball[] = []
  private rectCanvas: any
  private docElWidth: number

  private readonly canvas: HTMLCanvasElement
  private readonly speedMin: number
  private readonly speedMax: number
  private readonly bgColor: string
  private readonly ballsSetting: BallSetting[]
  private readonly docEl: HTMLElement
  private readonly designWidth: number
  private readonly scaleInPC: boolean
  private readonly resetOnResize: boolean
  private readonly resetOnlyWidth: boolean

  /**
   * @param canvas ç»æ¿ HTMLElement
   * @param balls æå®çå°çéåï¼åè®¸åç¬æå®éç½®
   * @param speedMin æå°ä½ç§»éåº¦ï¼åä½ï¼pxï¼é»è®¤ï¼-2
   * @param speedMax æå¤§ä½ç§»éåº¦ï¼åä½ï¼pxï¼é»è®¤ï¼2
   * @param bgColor canvas ç»å¸èæ¯é¢è²ï¼é»è®¤ï¼'transparent' éæ
   * @param docEl é¡µé¢èç¹ï¼æå¯ä»¥è§ä½é¡µé¢å®éæ¿è½½å®¹å¨çèç¹ï¼ï¼é»è®¤ï¼document.documentElement
   * @param designWidth è®¾è®¡ç¨¿çå®½åº¦ï¼é»è®¤ï¼375ï¼balls ä¸­éç½®çå°çå°ºå¯¸ï¼åºå½ä¸æ­¤å¤çè®¾è®¡ç¨¿å®½åº¦å¹éï¼
   * @param scaleInPC PCç«¯æ¯å¦èªå¨ç¼©æ¾ï¼é»è®¤ï¼trueï¼å³ç§»å¨ç«¯å PC ç«¯ç»ä¸é½èªå¨ç¼©æ¾
   * @param resetOnResize æµè§å¨ resize æç§»å¨ç«¯ orientationchange äºä»¶è§¦åæ¶ï¼æ¯å¦éç½®ç»å¸ï¼é»è®¤ï¼true
   * @param resetOnlyWidth æµè§å¨ resize æç§»å¨ç«¯ orientationchange äºä»¶è§¦åæ¶ï¼ä»å®½åº¦æååæ¶ï¼æä¼ resetï¼é»è®¤ï¼true
   */
  constructor({
    canvas,
    balls = [],
    speedMin = -2,
    speedMax = 2,
    bgColor = 'transparent',
    docEl = document.documentElement,
    designWidth = 375,
    scaleInPC = true,
    resetOnResize = true,
    resetOnlyWidth = true,
  }: CollisionConfig) {
    if (!canvas) {
      throw new Error('not valid canvas element')
    }

    this.canvas = canvas
    this.speedMin = speedMin
    this.speedMax = speedMax
    this.bgColor = bgColor === 'random' ? randomRgba() : bgColor
    this.ballsSetting = balls
    this.docEl = docEl
    this.docElWidth = this.docEl.clientWidth
    this.designWidth = designWidth
    this.scaleInPC = scaleInPC
    this.resetOnResize = resetOnResize
    this.resetOnlyWidth = resetOnlyWidth

    this._init()
  }

  /**
   * åå§å
   */
  private _init(): void {
    this.rectCanvas = this.canvas.getBoundingClientRect()

    // åå»ºå°ç
    this._create()
    // å¯å¨ï¼åå§éåº¦éæº
    this._run()
    // ç»å®äºä»¶
    this._bindEvents()
  }

  /**
   * åå»º ctxãballs
   *  - èªå¨å¤çä¸åå±å¹è®¡ç®éé
   */
  private _create(): void {
    const docWidth = this.docEl.clientWidth // é¡µé¢å¯è§åºåçå®½åº¦ï¼æå®éçé¡µé¢æå¤§å®½åº¦ï¼
    // ç¸å¯¹è®¾è®¡ç¨¿çç¼©æ¾åçï¼PC ç«¯å¯éç½®åæ°ä¸è¿è¡ç¼©æ¾ï¼
    const rateScale = !this.scaleInPC && !isMobile ? 1 : docWidth / this.designWidth
    // å°çåå¾ãåå¿åæ ï¼åç§å°ºå¯¸å±å¹ç¼©æ¾éé
    const ballsComputed = this.ballsSetting.map(ball => {
      return {
        ...ball,
        ...{
          r: ball.r * rateScale,
          x: ball.x * rateScale,
          y: ball.y * rateScale,
        },
      }
    })

    this.ctx = setupCanvas(this.canvas)
    this.balls = ballsComputed.map(
      ({ x, y, r, velX, velY, img, imgSrc, bgColor, speedMin, speedMax }) =>
        new Ball({
          ctx: <CanvasRenderingContext2D>this.ctx,
          x,
          y,
          r,
          img,
          imgSrc,
          bgColor: bgColor === 'random' ? randomRgba() : bgColor,
          velX: velX || random(speedMin || this.speedMin, speedMax || this.speedMax), // åè®¸è®¾ç½®åä¸ªå°ççåå§æ¨ªåä½ç§»éåº¦
          velY: velY || random(speedMin || this.speedMin, speedMax || this.speedMax), // åè®¸è®¾ç½®åä¸ªå°ççåå§çºµåä½ç§»éåº¦
          speedMin: speedMin || this.speedMin, // åè®¸è®¾ç½®åä¸ªå°ççæå°éåº¦
          speedMax: speedMax || this.speedMax, // åè®¸è®¾ç½®åä¸ªå°ççæå¤§éåº¦
        })
    )
  }

  /**
   * å¼å§è¿å¨
   */
  private _run: VoidFunction = () => {
    if (!this.ctx) {
      throw new Error('not valid ctx')
    }

    // æ¸ç©ºç»æ¿
    this.ctx.clearRect(0, 0, this.rectCanvas.width, this.rectCanvas.height)
    // ç»å¶èæ¯
    this.ctx.fillStyle = this.bgColor
    this.ctx.fillRect(0, 0, this.rectCanvas.width, this.rectCanvas.height)

    this.balls.map(ball => {
      ball.draw()
      this.balls = ball.detect(this.balls)
      ball.update()
    })

    this.raf = window.requestAnimationFrame(this._run)
  }

  private _bindEvents(): void {
    this.resetOnResize && window.addEventListener(resizeEvent, this._onResize, false)
  }

  private _onResize: VoidFunction = debounce(
    () => {
      const flag =
        (this.resetOnlyWidth && this.docElWidth !== this.docEl.clientWidth) || !this.resetOnlyWidth
      if (flag) {
        this.destroy()
        this._init()
      }
    },
    150,
    { maxWait: 1000 }
  )

  /**
   * éæ¯ animation frame
   */
  destroy(): void {
    const rect = this.canvas.getBoundingClientRect()

    window.removeEventListener(resizeEvent, this._onResize, false)
    window.cancelAnimationFrame(<number>this.raf)
    // æ¸ç©ºç»æ¿
    ;(<CanvasRenderingContext2D>this.ctx).clearRect(0, 0, rect.width, rect.height)
  }
}

export default Collision
