import { BallConfig } from './types'

class Ball {
  private img?: HTMLImageElement
  private imgSrc?: string
  private bgColor?: string
  private x: number
  private y: number
  private r: number
  private velX: number
  private velY: number
  private isLoading: boolean // æ§å¶å¾çå è½½ï¼é²æ­¢å¾çç»å®è¿å¤ onload

  private readonly ctx: CanvasRenderingContext2D
  private readonly speedMin: number
  private readonly speedMax: number

  /**
   * @param ctx canvas ç»å¾ context
   * @param img å°çå¯¹åºå¾çï¼Imageï¼
   * @param imgSrc å°çå¯¹åºå¾çç srcï¼BASE64æ http é¾æ¥ï¼
   * @param bgColor å°çèæ¯è²
   * @param x åå¿ x åæ 
   * @param y åå¿ y åæ 
   * @param r å°çåå¾
   * @param velX æ°´å¹³ä½ç§»éåº¦
   * @param velY åç´ä½ç§»éåº¦
   * @param speedMin æå°ä½ç§»éåº¦
   * @param speedMax æå¤§ä½ç§»éåº¦
   */
  constructor({ ctx, img, imgSrc, bgColor, x, y, r, velX, velY, speedMin, speedMax }: BallConfig) {
    this.ctx = ctx // ç»æ¿
    this.img = img
    this.imgSrc = imgSrc
    this.bgColor = bgColor
    this.x = x
    this.y = y
    this.r = r
    this.velX = velX
    this.velY = velY
    this.speedMin = speedMin
    this.speedMax = speedMax
    this.isLoading = false
  }

  /**
   * ç»å¶å°ç
   *  - å³ä½¿å¾çéåå½¢ä¹å¯ä»¥
   */
  draw(): void {
    this.ctx.save()
    this.ctx.beginPath()
    this.ctx.arc(this.x, this.y, this.r, 0, 2 * Math.PI)

    if (!!this.bgColor && typeof this.bgColor === 'string') {
      this.ctx.fillStyle = this.bgColor
      this.ctx.fill()
    }

    this.ctx.clip()

    if (!this.img && !!this.imgSrc && typeof this.imgSrc === 'string') {
      const img = new Image()

      if (/^data:/.test(this.imgSrc)) {
        // ä½¿ç¨ base64 ç¼ç 
        img.src = this.imgSrc
        this.img = img
      } else if (!this.isLoading) {
        this.isLoading = true
        // ä½¿ç¨ http é¾æ¥å è½½
        img.onload = () => {
          this.img = img
          this.isLoading = false
        }
        img.src = this.imgSrc
      }
    }

    this.img &&
      this.img instanceof Image &&
      this.ctx.drawImage(this.img, this.x - this.r, this.y - this.r, 2 * this.r, 2 * this.r)
    this.ctx.restore()
  }

  /**
   * æ£æµæå»
   * @param balls ç»å¸ä¸ææå°ç
   */
  detect(balls: Ball[]): Ball[] {
    return balls.map(ball => {
      if (this !== ball) {
        // å¤æ­ä¸å¶å®å°ççç¢°æè·ç¦»ï¼æé¤èªèº«ï¼
        const dxv = this.x + this.velX - (ball.x + ball.velX)
        const dyv = this.y + this.velY - (ball.y + ball.velY)
        const distance = Math.sqrt(dxv * dxv + dyv * dyv)

        if (distance <= this.r + ball.r) {
          // è·ç¦»å°äºä¸¤ä¸ªå°ççåå¿è·ç¦»ï¼åå¼¹
          const dvx = this.velX - ball.velX
          const dvy = this.velY - ball.velY
          const dx = this.x - ball.x
          const dy = this.y - ball.y
          const xx_yy = dx * dx + dy * dy
          const v_dvx = (dvx * dx * dx + dvy * dx * dy) / xx_yy
          const v_dvy = (dvy * dy * dy + dvx * dx * dy) / xx_yy

          this.velX = this._checkSpeed(this.velX - v_dvx)
          this.velY = this._checkSpeed(this.velY - v_dvy)
          ball.velX = this._checkSpeed(ball.velX + v_dvx)
          ball.velY = this._checkSpeed(ball.velY + v_dvy)
        }
      }
      return ball
    })
  }

  /**
   * æ´æ°ä½ç§»
   */
  update(): void {
    const canvas = this.ctx.canvas
    const rect = canvas.getBoundingClientRect()

    if (this.x + this.r >= rect.width) {
      this.velX = -this.velX
    }
    if (this.x - this.r <= 0) {
      this.velX = -this.velX
    }

    if (this.y + this.r >= rect.height) {
      this.velY = -this.velY
    }
    if (this.y - this.r <= 0) {
      this.velY = -this.velY
    }

    this.x += this.velX
    this.y += this.velY
  }

  /**
   * æ§å¶è¿å¨éåº¦
   * @param speed ä½ç§»éåº¦
   */
  private _checkSpeed(speed: number): number {
    if (speed > this.speedMax) {
      speed = this.speedMax
    } else if (speed < this.speedMin) {
      speed = this.speedMin
    }
    return speed
  }
}

export default Ball
